import React from "react";
import "./Card.css";
 
const Card = props => (
  <div className="card" onClick={() => props.seeArticle(props.id)}>
    <h5 className="card-title">{props.title}</h5>
    <a className="btn" href={props.url}>Save Article</a>
  </div>
);

export default Card;



